.skill-container {
    margin-top: 3rem;
    margin-bottom: 3rem;
}

ul {
    li {
        list-style-type: none;
        padding: 1rem 2rem;
    }
}

h3 {
    color: #fff;
}

.icons:hover {
    cursor: pointer;
}

.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;
}

.tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 3px 0;

    /* Position the tooltip */
    position: absolute;
    z-index: 1;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
}